import React, { useState } from 'react';
import { Layout, Button, message, Progress } from 'antd';
import FileUpload from './components/FileUpload';
import FileDownload from './components/FileDownload';
import { convertFile } from './api'; //  convertFile 
import './styles.css'; // 

const App: React.FC = () => {
    const [fileId, setFileId] = useState<string>('');
    const [conversionReady, setConversionReady] = useState<boolean>(false); // 
    const [isConverting, setIsConverting] = useState<boolean>(false); // 
    const [isDownloadEnabled, setIsDownloadEnabled] = useState<boolean>(false); // 

    const handleConversionReady = () => {
        setConversionReady(true);
    };

    const handleConvert = async () => {
        if (!fileId) {
            message.error('File ID is missing. Please upload a file first.');
            return;
        }
        setIsConverting(true); //  true
        try {
            console.log(fileId);
            const conversionResult = await convertFile(fileId); // 
            console.log('Conversion Result:', conversionResult.data); // 
            setFileId(conversionResult.data.fileId);
            message.success('Conversion completed successfully');
            setConversionReady(false); // 
            setIsDownloadEnabled(true); // 
        } catch (error) {
            message.error('Conversion failed.');
        } finally {
            setIsConverting(false); //  false
        }
    };

    return (
        <Layout>
            <Layout.Header>
                <h1>Tianditu2 Frontend</h1>
            </Layout.Header>
            <Layout.Content style={{ padding: '24px' }}>
                <FileUpload setFileId={setFileId} onConversionReady={handleConversionReady} />
                {conversionReady && fileId && ( //  fileId 
                    <>
                        <Button
                            type="primary"
                            onClick={handleConvert}
                            style={{ marginTop: '16px' }}
                            disabled={isConverting} // 
                        >
                            {isConverting ? 'Converting...' : 'Convert to Location'} 
                        </Button>
                    </>
                )}
                <div style={{ marginTop: '16px' }}> 
                    <FileDownload fileId={fileId} disabled={!isDownloadEnabled || !fileId} />
                </div>
            </Layout.Content>
        </Layout>
    );
};

export default App;