import axios, { AxiosProgressEvent } from 'axios';

const apiClient = axios.create({
    // 
    baseURL:  '/api',
});

// 
apiClient.interceptors.request.use(
    (config) => {
        // token
        config.headers.Authorization = `Bearer ${localStorage.getItem('token')}`;
        //  baseURL  url 
        config.url = `${config.url}`;
        console.log(config);
        return config;
    },
    (error) => {
        return Promise.reject(error);
    }
);

// 
apiClient.interceptors.response.use(
    (response) => {
        //  JSON 
        if (typeof response.data === 'string') {
            try {
                response.data = JSON.parse(response.data);
            } catch (e) {
                // 
            }
        }
        return response;
    },
    (error) => {
         // 
        if (error.response && error.response.status === 401) {
            // 401
            console.error('Unauthorized access');
        }
        return Promise.reject(error);
    }
);

export const uploadFile = async (file: File, onUploadProgress?: (progressEvent: AxiosProgressEvent) => void) => {
    const formData = new FormData();
    formData.append('file', file);
    return apiClient.post('/upload', formData, {
        headers: {
            'Content-Type': 'multipart/form-data',
        },
        onUploadProgress: onUploadProgress,
    });
};

export const downloadFile = async (fileId: string) => {
    return apiClient.get('/download', {
        params: { fileId }, //  fileId 
        responseType: 'blob',
    });
};

export const convertFile = async (fileId: string) => {
    return apiClient.get('/convert', { // :  post  get
        params: { fileId }, //  fileId 
    });
};